import { Component, OnInit } from '@angular/core';
import { RentableApiService } from '../rentable-api.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-loginpage-component',
  templateUrl: './loginpage-component.component.html',
  styleUrls: ['./loginpage-component.component.css']
})
export class LoginpageComponentComponent implements OnInit {

  url:string;
  constructor(rentableService$: RentableApiService, private _router:Router) {
       rentableService$.getSearchingResult(_router.url)
    .subscribe(result => {});
    this.url =_router.url;
   }

  ngOnInit() {
  }

}



