import { Component, OnInit } from '@angular/core';
import { RentableApiService } from '../../rentable-api.service';
import {NgForm} from '@angular/forms';

@Component({
  selector: 'app-postitembox-component',
  templateUrl: './postitembox-component.component.html',
  styleUrls: ['./postitembox-component.component.css']
})
export class PostitemboxComponentComponent implements OnInit {

  constructor(private rentableService$: RentableApiService) { }
  postItem(form: any){
    console.log(form.value);
    this.rentableService$.postItemToDb(form.value)
    .subscribe(result => { });
  }
  ngOnInit() {
  }

}



