import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-searchbox-component',
  templateUrl: './searchbox-component.component.html',
  styleUrls: ['./searchbox-component.component.css']
})
export class SearchboxComponentComponent implements OnInit {
  url: string;
  keyword: string;
  location: string;

  constructor() {
    this.url = '../';
    this.keyword = '';
    this.location = '';
   }

  toggleSearch()  {
    this. url = '../Search/'+ this.keyword + '/' + this.location;
  }
  ngOnInit() {
  }

}



