import { Component, OnInit } from '@angular/core';
import { RentableApiService } from '../../rentable-api.service';
import {NgForm} from '@angular/forms';
@Component({
  selector: 'app-signupbox-component',
  templateUrl: './signupbox-component.component.html',
  styleUrls: ['./signupbox-component.component.css']
})
export class SignupboxComponentComponent implements OnInit {

  constructor(private rentableService$: RentableApiService) { }

  postAccount(form: any){
    console.log(form.value);

    this.rentableService$.postAccountToDb(form.value)
    .subscribe(result => { });
  }
  ngOnInit() {
  }

}




