import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule} from '@angular/router';

import { HomepageComponentComponent } from './homepage-component/homepage-component.component';
import { SignuppageComponentComponent } from './signuppage-component/signuppage-component.component';
import { SearchpageComponentComponent } from './searchpage-component/searchpage-component.component';
import { PostitempageComponentComponent } from './postitempage-component/postitempage-component.component';
import { LoginpageComponentComponent } from './loginpage-component/loginpage-component.component';


const appRoutes: Routes = [
    {
        path: '',
        component: HomepageComponentComponent
    },
    {
        path: 'SignUp',
        component: SignuppageComponentComponent
    },
    {
        path: 'PostItem',
        component: PostitempageComponentComponent
    },
    {
        path: 'auth/google',
        component: LoginpageComponentComponent
    },
    {
        path: 'Search/:Keyword/:Location',
        component: SearchpageComponentComponent
    }

]

export const routing = RouterModule.forRoot(appRoutes);


