import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class RentableApiService {
  host:string = 'http://localhost:8080';
  idGenerator:number;

  account: rUser;
  post: rItem;
  constructor(private http: Http) { }

  getSearchingResult(url: string){
    return this.http.get(url)
    .map(response => response.json());
  }

  postAccountToDb(body: any){
    console.log("here2");
   // console.log(iFname);
    //this.account = { userID: 10, fName: iFname, lName: iLname, email: iemail, password: ipassword};
    return this.http.post("/CreateAccount/Create", body)
    .map(response => response.json());  
  }

  postItemToDb(body: any){
    console.log("here2");
   // console.log(iFname);
    //this.account = { userID: 10, fName: iFname, lName: iLname, email: iemail, password: ipassword};
    return this.http.post("/PostItem/Post", body)
    .map(response => response.json());  
  }

}
interface rUser{
    userID: number;
    fName: string;
    lName: string;
    email: string;
    password: string;
}
interface rItem{
  title: string;
    price: number;
    condition: number;
    description: string;
    image: string;
    location: string;
}


