import { Component, OnInit } from '@angular/core';
import { RentableApiService } from '../../rentable-api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-searchresult-component',
  templateUrl: './searchresult-component.component.html',
  styleUrls: ['./searchresult-component.component.css']
})
export class SearchresultComponentComponent implements OnInit {

  resultList: rItem[];
  url:string;
  constructor(rentableService$: RentableApiService, private _router:Router) {
       rentableService$.getSearchingResult(_router.url)
    .subscribe(result => { this.resultList = result;});
    this.url =_router.url;
   }

  ngOnInit() {
    
  }

}

interface rItem{
  title: String;
  price: number;
  condition: number;
  description: string;
  image: string;
  location: string;
}


