import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { APP_BASE_HREF } from '@angular/common';

import { AppComponent } from './app.component';
import { HeaderComponentComponent } from './header-component/header-component.component';
import { NavbarComponentComponent } from './navbar-component/navbar-component.component';
import { HometextComponentComponent } from './hometext-component/hometext-component.component';
import { SearchboxComponentComponent } from './searchbox-component/searchbox-component.component';
import { HowitworksComponentComponent } from './howitworks-component/howitworks-component.component';
import { FooterComponentComponent } from './footer-component/footer-component.component';
import { HomepageComponentComponent } from './homepage-component/homepage-component.component';
import { SignuppageComponentComponent } from './signuppage-component/signuppage-component.component';
import {routing} from './app.router';
import { SearchpageComponentComponent } from './searchpage-component/searchpage-component.component';
import { SearchresultComponentComponent } from './searchpage-component/searchresult-component/searchresult-component.component';
import { RentableApiService } from './rentable-api.service';
import { SignupboxComponentComponent } from './signuppage-component/signupbox-component/signupbox-component.component';
import { PostitempageComponentComponent } from './postitempage-component/postitempage-component.component';
import { PostitemboxComponentComponent } from './postitempage-component/postitembox-component/postitembox-component.component';
import { LoginpageComponentComponent } from './loginpage-component/loginpage-component.component';
import { LoginboxComponentComponent } from './loginpage-component/loginbox-component/loginbox-component.component';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponentComponent,
    NavbarComponentComponent,
    HometextComponentComponent,
    SearchboxComponentComponent,
    HowitworksComponentComponent,
    FooterComponentComponent,
    HomepageComponentComponent,
    SignuppageComponentComponent,
    SearchpageComponentComponent,
    SearchresultComponentComponent,
    SignupboxComponentComponent,
    PostitempageComponentComponent,
    PostitemboxComponentComponent,
    LoginpageComponentComponent,
    LoginboxComponentComponent,
  ],
  imports: [
    routing,
    BrowserModule,
    FormsModule,
    HttpModule
  ],
  providers: [ {provide: APP_BASE_HREF, useValue: '/'}, RentableApiService],
  bootstrap: [AppComponent]
})
export class AppModule { }



